// 路由懒加载
// 注册
const register = r => require.ensure([], () => r(require('../page/register/register')), 'register')
// 登录
const login = r => require.ensure([], () => r(require('../page/login/login')), 'login')
// 公共头，中，尾
const comHMF = r => require.ensure([], () => r(require('../page/comHMF/comHMF')), 'comHMF')
// 管理员
const showAdmin = r => require.ensure([], () => r(require('../page/showAdmin/showAdmin')), 'showAdmin')
// 委托
const tabsEntrust = r => require.ensure([], () => r(require('../page/tabsEntrust/tabsEntrust')), 'tabsEntrust')
// 撤销
const tabsRevocation = r => require.ensure([], () => r(require('../page/tabsRevocation/tabsRevocation')), 'tabsRevocation')
// 机构信息管理关联页
const tabsSee = r => require.ensure([], () => r(require('../page/tabsSee/tabsSee')), 'tabsSee')

export const routes = [
    {
        path: '/',
        redirect:'/login'
      },
      // 注册
      {
        path: '/register',
        name: 'register',
        component: register
      },
      // 登录
      {
        path: '/login',
        name: 'login',
        component: login
      },
      // 公共头，中，尾
      {
        path: '/comHMF',
        name: 'comHMF',
        component: comHMF,
        meta: {
          requiresAuth: true
        }
      },
      // 用户操作页
      {
        path: '/tabsEntrust',
        name: 'tabsEntrust',
        component: tabsEntrust,
        meta: {
          requiresAuth: true
        }
      },
      // 订单审核页
      {
        path: '/tabsRevocation',
        name: 'tabsRevocation',
        component: tabsRevocation,
        meta: {
          requiresAuth: true
        }
      },
      // 管理员页
      {
        path: '/showAdmin',
        name: 'showAdmin',
        component: showAdmin,
        meta: {
          requiresAuth: true
        }
      },
      // 
      {
        path: '/tabsSee',
        name: 'tabsSee',
        component: tabsSee,
        meta: {
          requiresAuth: true
        }
      },
]


