// 增加一个常量对象
export const state = {
	token:"",
	customerId: '',
	id: '',
	password: '',
	role: '',
	userid: '',
	username: '',
}
console.log(state)
// 定义改变state状态的回调函数
export const mutations = {
	changeStateToken(state,token){
		state.token = token
	},
	logout(state){
		state.token = null
	},
	changeStateCustomerId(state,customerId){
		state.customerId = customerId
	},
	changeStateID(state,id){
		state.id = id
	},
	changeStatePassword(state,password){
		state.password = password
	},
	changeStateRole(state,role){
		state.role = role
	},
	changeStateUserid(state,userid){
		state.userid = userid
	},
	changeStateUsername(state,username){
		state.username = username
	}
}


