/*接口地址*/

import {baseUrl} from '../config/index'

const authBase = baseUrl.atgo
const authBaseWebsocket = baseUrl.atgoWebsocket

export const domain = {
	authbaseUrl:`${authBase}/user/login`,
	logGetCod:`${authBase}/sms/send/`,//获取验证码
	logCheck:`${authBase}/user/check/`,//效验手机号
	logValidate:`${authBase}/sms/validate/`,//效验验证码
	logLogin:`${authBase}/order/login/`,//登录
	indFindCustomer:`${authBase}/user/findCustomer/`,//获取产品名
	indFindStrategy:`${authBase}/user/findStrategy/`,//根据产品名展示策略
	indMd:`${authBase}/order/md`,//委托生成订单
	insAddCustomer:`${authBase}/user/addCustomer/`,//添加机构
	insDelCustomer:`${authBase}/user/delCustomer/`,//删除机构
	accQueryCustomer:`${authBase}/user/queryCustomer`,//获取机构名
	accGetCustomer:`${authBase}/user/getCustomer/`,//根据机构名显示账户
	accAddClient:`${authBase}/user/addClient/`,//保存添加账户信息
	accDelClient:`${authBase}/user/delClient/`,//根据机构名删除账户
	useGetUser:`${authBase}/user/getUser/`,//根据机构名获取母账户信息
	useGetphone:`${authBase}/user/getphone/`,//根据机构名获取电话信息
	useAddUser:`${authBase}/user/addUser`,//提交添加新用户信息
	useDelCustomerUser:`${authBase}/user/delCustomerUser/`,//删除机构对应用户
	ordQueryOrder:`${authBase}/order/queryOrder/`,//获取需要审核的订单
	ordSendOrder:`${authBase}/order/sendOrder`,//点击审核订单
	perActualCsvFile:`${authBase}/strategy/actualCsvFile/`,//下载报表
	personUpdatePwd:`${authBase}/order/updatePwd/`,//修改密码
	comOrdinaryNewActualOrder:`${authBase}/order/newActualOrder`,//普通单下单
	comOrdinaryCancelActualOrder:`${authBase}/order/cancelActualOrder/`,//普通单撤单
	comOrdinaryReturnActualClientId:`${authBase}/order/returnActual8ByClientId/`,//某一账户回报
	comOrdinaryReturnActualUser:`${authBase}/order/returnActual8ByUser/`,//回报
	authBaseWebsocketOrdinary:`${authBaseWebsocket}`,//普通单全部回报
}



