const Base64 = require('./base64').Base64;

// export default {
const config = {
	// 取整除100的数
	goAliquot(num) {
		var as = /^[1-9]\d*$/;
		if(as.test(num / 100)){
			return num
		}else{
			return false;
		}
	},
	// 获取当前时间
	getNowFormatDate:function () {
		var date = new Date();
		var seperator1 = "-";
		var seperator2 = ":";
		var month = date.getMonth() + 1;
		var strDate = date.getDate();
		if (month >= 1 && month <= 9) {
			month = "0" + month;
		}
		if (strDate >= 0 && strDate <= 9) {
			strDate = "0" + strDate;
		}
		var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
				+ " " + date.getHours() + seperator2 + date.getMinutes()
				+ seperator2 + date.getSeconds();
		return currentdate;
	},
	//判断是否为空
	isNull : function (value) {
		return typeof value == 'undefined' || value === '' || value == null || value == 'undefined';
	},
	isNotNull : function (value) {
		return !this.isNull(value);
	},
	altPrompt : function (message) {
		//创建prompt框样式
		var type = "width:60%;padding:28px 12px;line-height:24px;border-radius:10px;color:#fff;background-color:#333333;text-align:center;font-size:17px;top:43vh;opacity:0;position:fixed;left:20%;z-index:1003;";
		//创建prompt框
		var h = $("<div style='" + type + "' id='e_ui_prompt'>" + message + "</div>");
		$('body').append(h);

		//改变透明度
		var i = 0;
		var t = setInterval(function () {
			i = i + 0.1;
			if (i >= 1) {
				clearInterval(t);
				i = 0;
			} else {
				$(h).css('opacity', i);
			}
		}, 80);

		// 2秒后消失
		setTimeout(function () {

			var j = $(h).css('opacity');
			var ti = setInterval(function () {
				j = j - 0.1;
				if (j <= 0) {
					clearInterval(ti);
					$(h).remove();
				} else {
					$(h).css('opacity', j);
				}
			}, 80);
		}, 2000);
	},
	//倒计时
	daojishi : function (wait) {
		//    var wait = 60;
		function time() {
			if (wait == 0) {
				$('#getCod').text('请重新获取');
				$('#getCod').removeClass('disabled');
			} else {
				$('#getCod').text('剩余时间' + wait + 's');
				$('#getCod').addClass('disabled');
				wait--;
				setTimeout(function () {
					time();
				}, 1000)
			}
		}
		//clearInterval(time());
		time();
	},
	//加密
	encrypt : function (key, value) {
		localStorage.setItem(key, (Base64.encode(value)));
	},
	//解密
	decipher : function (key) {
		var returnValue = localStorage.getItem(key);
		if (this.isNull(returnValue)) {
			returnValue = null;
		} else {
			returnValue = Base64.decode(returnValue);
		}
		return returnValue;
	},
	// 清除缓存
	removeCache:function (key) {
		localStorage.removeItem(key);
	},
	//获取问号后面内容
	getSi:function () {
		var href = window.location.href;
		var index = href.indexOf('?');
		if (index > -1) {
			return href.substring(index + 1, href.length);
		}
		return null;
	},

	//获取路径参数
	getHash:function (key, url) {
		var hash;
		if (!!url) {
			hash = url.replace(/^.*?[#](.+?)(?:\?.+)?$/, "$1");
			hash = (hash == url) ? "" : hash;
		} else {
			hash = self.location.hash;
		}
		hash = "" + hash;
		hash = hash.replace(/^[?#]/, '');
		hash = "&" + hash;
		var val = hash.match(new RegExp("[\&]" + key + "=([^\&]+)", "i"));
		if (val == null || val.length < 1) {
			return null;
		} else {
			return decodeURIComponent(val[1])
		}

	},


	//转化日期字符串
	parseDate : function (dataStr) {
		if (dataStr == '') {
			return null;
		}
		var _dataStr = new String(dataStr).replace(/\-/g, '/').replace(/\.\d+$/, '');
		var date = new Date(_dataStr);
		if (date.toString !== 'Invalid Date') {
			return date;
		}
		return null;
	},

	//格式化日期字符串/日期对象，返回格式'2012-12-21'
	formatterDate : function (date) {
		var _d = new Date();
		if (typeof date == 'object') {
			_d = date;
		} else if (typeof date == 'string') {
			_d = common.parseDate(date);
		}

		var y = _d.getFullYear();
		var m = _d.getMonth() + 1;
		var d = _d.getDate();

		return y + '-' + (m < 10 ? ('0' + m) : m) + '-' + (d < 10 ? ('0' + d) : d);
	},

	//手机号格式化，param文本输入框
	mobileFormat : function (param) {
		console.log(param, param.val())
		if (param.val().length == 11) {
			var phone = param.val().substring(0, 3) + " " + param.val().substring(3, 7) + " " + param.val().substring(7, param.val().length);
			param.val(phone);
		} else {
			var phone = param.val().replace(/\s/g, "");
			param.val(phone);
		}
	},

	//校验用户名
	nameCheck : function (name) {
		var strExp = new RegExp(/^[\u4e00-\u9fa5_A-Za-z]+$/);
		if (name.replace(/\s/g, "").length == 0) {
			common.altPrompt('请输入用户名');
			return false;
		} else if (strExp.test(name)) {
			return true;
		} else {
			common.altPrompt('用户名错误')
			return false;
		}
	},


	//密码校验
	passwordCheck : function (password) {
		//	var myreg = /(?!^[0-9]+$)(?!^[A-z]+$)(?!^[^A-z0-9]+$)^.{6,20}$/;
		var myreg = /^[0-9a-zA-Z_]{5,17}$/;
		if (password.replace(/\s/g, "").length == 0) {
			common.altPrompt('请输入密码！');
			return false;
		}
		if (!myreg.test(password.replace(/\s/g, ""))) {
			common.altPrompt('密码格式不正确');
			return false;
		}
		return true;
	},

	// 手机号校验mobile 传入手机号
	mobileCheck : function (mobile) {
		var myreg = /^(((13[0-9]{1})|(14[0-9]{1})|(15[0-9]{1})|(16[0-9]{1})|17[1-9]{1}|(18[0-9]{1})|(19[0-9]{1}))+\d{8})$/;
		//	var myregError = /^((170)+\d{8})$/;
		if (mobile.replace(/\s/g, "").length == 0) {
			common.altPrompt('请输入手机号');
			return false;
		}
		if (mobile.replace(/\s/g, "").length != 11) {
			common.altPrompt('请输入11位手机号码');
			return false;
		}
		//	if(myregError.test(mobile.replace(/\s/g, ""))) { 
		//		common.altPrompt('手机号不符合申请资格，请您更换'); 
		//		return false; 
		//	}
		if (!myreg.test(mobile.replace(/\s/g, ""))) {
			common.altPrompt('手机号格式不正确');
			return false;
		}
		return true;
	},

	//验证码校验
	codCheck : function (cod) {
		var myreg = /^[0-9]{6}$/;
		if (cod.replace(/\s/g, "").length == 0) {
			common.altPrompt('请输入验证码！');
			return false;
		}
		if (!myreg.test(cod.replace(/\s/g, ""))) {
			common.altPrompt('验证码格式不正确');
			return false;
		}
		return true;
	},
	// 分割转换时间戳，timestamp是一个数组
	changeTimeStamp:function(timestamp){
		let valueTimeStar = '';
		let valueTimeEnd = '';
		for(let i = 0;i<timestamp.length;i++){
            console.log(timestamp[i])
            valueTimeStar = timestamp[0];
            valueTimeEnd = timestamp[1]
        }
		return {
			valueTimeStar:valueTimeStar,
			valueTimeEnd:valueTimeEnd
		}
	},
	// 时间戳转化时间yy-mm-dd
	changeTime:function(times){
		let date = new Date(times);  
		let date_value=date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate() + ' ' + date.getHours() + ':' + date.getMinutes() + ':' + date.getSeconds();  
		// let date_value=date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate();  
		//yyyy-MM-dd hh:mm:ss
		return date_value;
	},
	// 分页
	Page:function(opt){
		var set = $.extend({num:null,startnum:1,elem:null,callback:null},opt||{});
		if(set.startnum>set.num||set.startnum<1){
			set.startnum = 1;
		}
		var n = 0,htm = '';
		var clickpages = {
			elem:set.elem,
			num:set.num,
			callback:set.callback,
			init:function(){
				this.elem.next('div.pageJump').children('.button').unbind('click')
				this.JumpPages();
				this.elem.children('li').click(function () {
					var txt = $(this).children('a').text();
					var page = '', ele = null;
					var page1 = parseInt(clickpages.elem.children('li.active').attr('page'));
					if (isNaN(parseInt(txt))) {
						switch (txt) {
							case '下一页':
								if (page1 == clickpages.num) {
									return;
								}
								if (page1 >= (clickpages.num - 2) || clickpages.num <= 6 || page1 < 3) {
									ele = clickpages.elem.children('li.active').next();
								} else {
									clickpages.newPages('next', page1 + 1);
									ele = clickpages.elem.children('li.active');
								}
								break;
							case '上一页':
								if (page1 == '1') {
									return;
								}
								if (page1 >= (clickpages.num - 1) || page1 <= 3 || clickpages.num <= 6) {
									ele = clickpages.elem.children('li.active').prev();
								} else {
									clickpages.newPages('prev', page1 - 1);
									ele = clickpages.elem.children('li.active');
								}
								break;
							case '«':
								if (page1 == '1') {
									return;
								}
								if (clickpages.num > 6) {
									clickpages.newPages('«', 1);
								}
								ele = clickpages.elem.children('li[page=1]');
								break;
							case '»':
								if (page1 == clickpages.num) {
									return;
								}
								if (clickpages.num > 6) {
									clickpages.newPages('»', clickpages.num);
								}
								ele = clickpages.elem.children('li[page=' + clickpages.num + ']');
								break;
							case '...':
								return;
						}
					} else {
						if ((parseInt(txt) >= (clickpages.num - 3) || parseInt(txt) <= 3) && clickpages.num > 6) {
							clickpages.newPages('jump', parseInt(txt));
						}
						ele = $(this);
					}
					page = clickpages.actPages(ele);
					if (page != '' && page != page1) {
						if (clickpages.callback){
							clickpages.callback(parseInt(page));
						}
					}
				});
			},
			//active
			actPages:function (ele) {
				ele.addClass('active').siblings().removeClass('active');
				return clickpages.elem.children('li.active').text();
			},
			JumpPages:function () {
				this.elem.next('div.pageJump').children('.button').click(function(){
					var i = parseInt($(this).siblings('input').val());
					if(isNaN(i)||(i<=0)||i>clickpages.num){
						return;
					}else if(clickpages.num>6){
						clickpages.newPages('jump',i);
					}else{
						var ele = clickpages.elem.children('li[page='+i+']');
						clickpages.actPages(ele);
						if (clickpages.callback){
							clickpages.callback(i);
						}
						return;
					}
					
					if (clickpages.callback){
						clickpages.callback(i);
					}
				})
			},

			//newpages
			newPages:function (type, i) {
				var html = "",htmlLeft="",htmlRight="",htmlC="";
				var HL = '<li><a>...</a></li>';
				html = '<li><a  aria-label="Previous">&laquo;</a></li>\
					<li><a>上一页</a></li>'
				for (var n = 0;n<3;n++){
					htmlC += '<li '+((n-1)==0?'class="active"':'')+' page="'+(i+n-1)+'"><a>'+(i+n-1)+'</a></li>';
					htmlLeft += '<li '+((n+2)==i?'class="active"':'')+' page="'+(n+2)+'"><a>'+(n+2)+'</a></li>';
					htmlRight += '<li '+((set.num+n-3)==i?'class="active"':'')+' page="'+(set.num+n-3)+'"><a>'+(set.num+n-3)+'</a></li>';
				}
				
				switch (type) {
					case "next":
						if(i<=4){
							html+='<li page="1"><a>1</a></li>'+htmlLeft+HL+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
						}else if(i>=(set.num-3)){
							html+='<li page="1"><a>1</a></li>'+HL+htmlRight+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
						}else{
							html += '<li page="1"><a>1</a></li>'+HL+htmlC+HL+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
						}
						break;
					case "prev":
						if(i<=4){
							html+='<li page="1"><a>1</a></li>'+htmlLeft+HL+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
						}else if(i>=(set.num-3)){
							html+='<li page="1"><a>1</a></li>'+HL+htmlRight+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
						}else{
							html += '<li page="1"><a>1</a></li>'+HL+htmlC+HL+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
						}
						break;
					case "«" :
						html+='<li class="active" page="1"><a>1</a></li>'+htmlLeft+HL+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
						break;
					case "»" :
						html+='<li page="1"><a>1</a></li>'+HL+htmlRight+'<li class="active" page="'+set.num+'"><a>'+set.num+'</a></li>';
						break;
					case "jump" :
						if(i<=4){
							if(i==1){
								html+='<li class="active" page="1"><a>1</a></li>'+htmlLeft+HL+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
							}else{
								html+='<li page="1"><a>1</a></li>'+htmlLeft+HL+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
							}
						}else if((i>=set.num-3)&&(set.num>=7)){
							if(i==set.num){
								html+='<li page="1"><a>1</a></li>'+HL+htmlRight+'<li class="active" page="'+set.num+'"><a>'+set.num+'</a></li>';
							}else{
								html+='<li page="1"><a>1</a></li>'+HL+htmlRight+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
							}
						}else{
							html += '<li page="1"><a>1</a></li>'+HL+htmlC+HL+'<li page="'+set.num+'"><a>'+set.num+'</a></li>';
						}
				}
				html += '<li><a>下一页</a></li>\
					<li><a  aria-label="Next">&raquo;</a></li>';
				if (this.num > 5 || this.num < 3) {
					set.elem.html(html);
					clickpages.init({num:set.num,elem:set.elem,callback:set.callback});
				}
			}
		}
		if(set.num<=1){
			$(".pagination").html('');
			return;
		}else if(parseInt(set.num)<=6){
			n = parseInt(set.num);
			var html='<li><a  aria-label="Previous">&laquo;</a></li>\
					<li><a>上一页</a></li>';
			for(var i=1;i<=n;i++){
				if(i==set.startnum){
					html+='<li class="active" page="'+i+'"><a>'+i+'</a></li>';
				}else{
					html+='<li page="'+i+'"><a>'+i+'</a></li>';
				}
			}
			html +='<li><a>下一页</a></li>\
					<li><a  aria-label="Next">&raquo;</a></li>';
			set.elem.html(html);
			clickpages.init();
		}else{
			clickpages.newPages("jump",set.startnum)
		}
}
}

// module.exports = config;
export default config


