// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueAwesomeSwiper from 'vue-awesome-swiper'
// import Vuex from 'vuex'
import ElementUI from 'element-ui'
// 
import {router} from './router/router'
import store from './store'
import axios from './api/index'

//引入公用css,js 
import 'element-ui/lib/theme-chalk/index.css';
import '@/assets/css/style.css'
import '@/utils/jquery-2.1.4.min.js'
import '@/assets/css/swiper.min.css'
import '@/assets/css/pager.css'

// import domain from '@/utils/interface.js'
import {domain} from '@/api/serverUrl'
global.domain = domain;


Vue.config.productionTip = false;

Vue.use(VueAwesomeSwiper)
Vue.use(ElementUI)


/* eslint-disable no-new */
new Vue({
  el: '#app',// index.html中的id=app的div 
  router,
  axios,
  store,//使用store状态管理
  components: { App },// 这里的App等于App: App ，名称要和模板名称一样，上面的template中写的App,这里也要是App
  template: '<App/>',// 这里是把'./App'的内容加到这个模板中 
  // 监听路由
  watch:{
    '$route':function(to,from,next){
      console.log(to)
    }
  }
})



